<template>
	<swiper :options="swiperOption" ref="mySwiper">
		<swiper-slide>I'm Slide 1</swiper-slide>
		<swiper-slide>I'm Slide 2</swiper-slide>
		<swiper-slide>I'm Slide 3</swiper-slide>
		<swiper-slide>I'm Slide 4</swiper-slide>
		<swiper-slide>I'm Slide 5</swiper-slide>
		<swiper-slide>I'm Slide 6</swiper-slide>
		<swiper-slide>I'm Slide 7</swiper-slide>
		<swiper-slide>I'm Slide 8</swiper-slide>
		<swiper-slide>I'm Slide 9</swiper-slide>
		<swiper-slide>I'm Slide 10</swiper-slide>
		<swiper-slide>I'm Slide 11</swiper-slide>
		<swiper-slide>I'm Slide 12</swiper-slide>
		<swiper-slide>I'm Slide 13</swiper-slide>
		<swiper-slide>I'm Slide 14</swiper-slide>
		<div class="swiper-pagination" slot="pagination"></div>
	</swiper>
</template>
<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper'

  export default {
    name: 'carrousel',
    data() {
      return {
        swiperOption: {
          direction: 'vertical',
          effect: "flip",
          grabCursor: true,
          setWrapperSize: true,
          loop : true,
          pagination: {
            el: '.swiper-pagination',
            type: 'fraction',
            clickable: true,
            renderFraction: function (currentClass, totalClass) {
              return '<span class="' + currentClass + '"></span>' +
                '/' +
                '<span class="' + totalClass + '"></span>';
            }
          },
          mousewheelControl: true,
          observeParents: true
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    mounted() {
      document.querySelector('.swiper-wrapper').style.height = '100vh';
      document.querySelector('.swiper-wrapper').style.overflow = 'hidden';
    }
  }
</script>
<style scoped>
	body{
		margin: 0;
		padding: 0;
	}
	.swiper-container {
		height: 100vh;
	}
	.swiper-slide{
		text-align: center;
		line-height: 40px;
	}
</style>



